<template>
    <div>
        <img src="https://hit-ocr.online/uploaded/image.jpg" alt="" >
        <div id="test">
            <button type="button" @click="cropImage" class="btn btn-sm btn-dark">Crop Image</button>
        </div>
    </div>
</template>
<script>
export default{
    data(){
        return{
            boxes:[ { "x": 362, "width": 533, "y": 468, "height": 30 }, { "x": 360, "width": 589, "y": 500, "height": 38 }, { "x": 359, "width": 541, "y": 627, "height": 45 } ]
        }
    },
    methods: {
        cropImage(){
            const imgElement = document.querySelector('img');
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            const img = new Image();

            img.onload = () => {
                for (const box of this.boxes) {
                const { x, y, width, height } = box;
                canvas.width = width;
                canvas.height = height;
                ctx.drawImage(img, x, y, width, height, 0, 0, width, height);
                const dataUrl = canvas.toDataURL();
                const croppedImage = new Image();
                croppedImage.src = dataUrl;
                    document.getElementById('test').appendChild(croppedImage)
                }
            };
            img.setAttribute('crossorigin', 'anonymous'); // works for me

            img.src = imgElement.src;
        }
    },
}
</script>